<template>
    <v-container
        class="fill-height"
        fluid
      >
      toStore: {{toStore}}
      <br>
      userInfo: {{userInfo}}
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
          >
            <v-card class="elevation-12">
              <v-toolbar
                color="primary"
                dark
                flat
              >
                <v-toolbar-title>Login form</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field
                    label="Login"
                    name="login"
                    @blur="onLoadUser()"
                    v-model="idNumber"
                    prepend-icon="mdi-account"
                    type="text"
                  ></v-text-field>

                  <v-text-field
                    id="password"
                    label="Password"
                    name="password"
                    v-model="password"
                    prepend-icon="mdi-lock"
                    type="password"
                    @keyup.enter="onLogin()"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn @click="onLogin()" color="primary">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
</template>
<script>
import md5 from 'md5-hex' 
import axios from 'axios' 

export default {
    data(){
        return{
            idNumber: "",
            password: "",
            toStore: {},
            masterListUrl: "http://hrdapps48:5031/masterlist/id2/"
        }
    },
    mounted(){
      console.log(md5("00002"))
    },
    methods: {
        onLoadUser(){
            axios.get(`${this.masterListUrl}${this.idNumber}`)
            .then(res => {
                console.log(res.data)
                if(res.data.employeeCode){
                  
                    this.toStore = res.data
                }             
            })
            .catch(() => {
              alert('This user account does not exist.')
            })
        },
        onLogin(){
          if(this.toStore.password === md5(this.password)){
            // alert('Allow to access')
            this.CHANGE_USERINFO(this.toStore)
            this.$router.push("/")
          }else if(this.password === ""){
            alert('Please input password')
          }else {
            alert('Please check user and password')
          }
        }
    }
}
</script>